.company-launch-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px;
  background-color: #ffffff;
}

.text-section {
  flex: 1;
  padding-right: 20px;
}

.text-section h1 {
  font-size: 2.5em;
  font-weight: bold;
  margin: 0 0 20px;
  color: #000000;
  line-height: 1.2;
}

.text-section p {
  font-size: 1.1em;
  color: #333333;
  margin-bottom: 30px;
  line-height: 1.6;
}

.launch-button {
  background-color: #ffb703;
  color: #ffffff;
  padding: 12px 20px;
  font-size: 1em;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.launch-button:hover {
  background-color: #bde0fe;
  color: #2f3e46;
}

.image-section {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}

.team-image {
  border-radius: 50%;
  object-fit: cover;
}

.button {
  display: inline-block;
  padding: 10px 20px;
  border: 2px solid #3772ff;
  text-transform: uppercase;
  text-decoration: none;
  color: #3772ff;
  font-weight: bold;
  border-radius: 5px;
  background-color: transparent;
  transition: background 0.3s ease, color 0.3s ease, transform 0.2s ease;
}

.button:hover {
  background: linear-gradient(45deg, #50c9c3, #3772ff);
  color: #fff;
  transform: translateY(-2px);
}

/* Tablet Styles */
@media (max-width: 1024px) {
  .company-launch-container {
      flex-direction: column;
      padding: 30px;
  }

  .text-section {
      padding-right: 0;
      text-align: center;
  }

  .text-section h1 {
      font-size: 2em;
  }

  .text-section p {
      font-size: 1em;
  }

  .image-section {
      justify-content: center;
      margin-top: 20px;
  }

  .team-image {
      width: 80%;
  }
}

/* Mobile Styles */
@media (max-width: 768px) {
  .company-launch-container {
      padding: 20px;
  }

  .text-section h1 {
      font-size: 1.8em;
  }

  .text-section p {
      font-size: 0.95em;
      margin-bottom: 20px;
  }

  .launch-button {
      font-size: 0.9em;
      padding: 10px 16px;
  }

  .button {
      font-size: 0.9em;
      padding: 8px 16px;
  }

  .team-image {
      width: 70%;
  }
}

/* Small Mobile Styles */
@media (max-width: 480px) {
  .text-section h1 {
      font-size: 1.5em;
  }

  .text-section p {
      font-size: 0.9em;
  }

  .team-image {
      width: 60%;
  }

  .launch-button, .button {
      padding: 8px 14px;
      font-size: 0.85em;
  }
}
